// @flow

import React from 'react'
import {useStaticQuery, graphql, Link} from 'gatsby'
import styled from 'styled-components'

import {rhythm, scale} from '../utils/typography'

declare var __PATH_PREFIX__: any

type Props = {
  location: {
    pathname: string,
  },
  title: string,
  children: any,
}

const FONT_NANUM_PEN_SCRIPT: string = 'Nanum Pen Script'
const FONT_NANUM_GOTHIC: string = 'Nanum Gothic'

const H1 = styled.h1`
  font-family: ${FONT_NANUM_PEN_SCRIPT};
  margin-bottom: ${rhythm(1.5)};
  margin-top: 0px;
  font-weight: 100;
`

const H3 = styled.h3`
  font-family: ${FONT_NANUM_PEN_SCRIPT};
  font-size: 30px;
  margin-top: 0px;
`

const Container = styled.div`
  margin-left: auto;
  margin-right: auto;
  max-width: ${rhythm(24)};
  padding: ${rhythm(1.5)} ${rhythm(3 / 4)};
`

const MyLink = styled(Link)`
  box-shadow: none;
  text-decoration: none;
  color: inherit;
`

const MyFooter = styled.footer`
  font-family: ${FONT_NANUM_GOTHIC};
`

const Layout = (props: Props) => {
  const data = useStaticQuery(graphql`
    query AuthorQuery {
      site {
        siteMetadata {
          author {
            name
          }
        }
      }
    }
  `)

  const {location, title, children} = props

  const rootPath = `${__PATH_PREFIX__}/`
  let header

  if (location.pathname === rootPath) {
    header = (
      <H1
        style={{
          ...scale(1.5),
        }}>
        <MyLink to="/">{title}</MyLink>
      </H1>
    )
  } else {
    header = (
      <H3>
        <MyLink to="/">{title}</MyLink>
      </H3>
    )
  }

  const {author} = data.site.siteMetadata

  return (
    <Container>
      <header>{header}</header>
      <main>{children}</main>
      <MyFooter>
         {new Date().getFullYear()}, Built with
        {` `}
        <a href="/">{author.name}</a>
      </MyFooter>
    </Container>
  )
}

export default Layout
